export default {
	data() {
		return {
			list: []
		}
	},
	onLoad(options) {
		this.$http.get({
			url: '/v1/rest/club/clubList',
			data: {
				userId: options.userId
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.list = resp.data
			}
		})
	},
	methods: {
		// 活动详情
		toDetail(item) {
			console.log(item)
			uni.navigateTo({
				url: '/pages/club/homePage/homePage?clubId=' + item.clubId
			})
		},
		// 会费详情
		toMoneyDetail(item) {
			uni.navigateTo({
				url: '/pages/club/clubMoney/clubMoney?clubId=' + item.clubId
			})
		},
		// 会费充值
		toclubRecharge(item) {
			uni.navigateTo({
				url: '/pages/userCenter/membershipFeeRecharge/membershipFeeRecharge?clubId=' + item.clubId
			})
		}
	}
}
